import Image from "next/image";
import styles from "./page.module.css";
import ProductList from "./component/ProductList";
import { Product } from './component/interface';
import banner from "./img/bnpolo.jpg";
import data from "./data.json"; // import từ JSON file

// Chuyển đổi từ JSON về định dạng Product[]
const products: Product[] = data.product.map((p: any) => ({
  id: p.product_id,
  name: p.title,
  price: p.price,
  image: p.image,
  description: p.description,
  category: p.category_id
}));

const featuredProducts = products.slice(0, 4);

export default function Home() {
  return (
    <div>
      {/* Banner */}
      <div className={styles.banner}>
        <Image 
          src={banner}
          alt="Banner quảng cáo"
          fill 
          style={{ objectFit: 'cover' }}
          priority
        />
      </div>

      {/* Danh sách sản phẩm nổi bật */}
      <ProductList props={{
        title: "Sản Phẩm Nổi Bật", 
        products: featuredProducts
      }}/>

      {/* Sản phẩm mới */}
      <div className={styles.newProductsSection}>
        <ProductList props={{
          title: "Sản Phẩm Mới", 
          products: products
        }}/>
      </div>

      {/* Tất cả sản phẩm */}
      <ProductList props={{
        title: "Tất Cả Sản Phẩm", 
        products: products
      }}/>
    </div>
  );
}
